<template>
    <div id="login-content-div">
        <b-row no-gutters>
            <b-col cols="8">
                <div class="home-image-div">
                    <img class="home-image" :src="require('@/assets/img/home.jpg')" alt="" />
                </div>
            </b-col>
            <b-col cols="4">
                <div class="login">
                    <div class="login-content">
                        <form v-on:submit="checkForm" name="login_form">
                            <h1 class="" style="color: #5B645F;">Welcome</h1>
                            <div class="text-muted  mb-4">
                                Please sign into your account.
                            </div>

                            <b-form-group label="Username">
                                <b-input size="lg" class="fs-15px" autocomplete="username" v-model="username"
                                    placeholder="username@address.com"></b-input>
                            </b-form-group>

                            <b-form-group>
                                <div class="d-flex">
                                    <label>Password</label>
                                    <b-link a href="#" class="ml-auto text-muted">Forgot password?</b-link>
                                </div>
                                <b-input type="password" size="lg" autocomplete="current-password" class="fs-15px"
                                    v-model="password" placeholder="*************"></b-input>
                                    <span class="feedback-validation-msg" v-if="loginValidation">
                                        {{ loginValidationMsg }}
                                    </span>
                            </b-form-group>

                            

                            <b-form-group>
                                <b-form-checkbox id="rememberMe" name="remember_me">Remember me</b-form-checkbox>
                            </b-form-group>

                            <button type="submit" class="btn btn-primary btn-lg btn-block fw-500 mb-3">LOGIN</button>
                            <!-- <div class="text-center text-muted">
                                Don't have an account yet? <router-link to="/page/register">Sign up</router-link>.
                            </div> -->
                        </form>
                    </div>
                </div>
            </b-col>
        </b-row>
    </div>
</template>

<script>
import AppOptions from '../config/AppOptions.vue'

export default {
    data() {
        return {
            username: '',
            password: '',
            loginValidation : false,
            loginValidationMsg : ''
        }

    },
    mounted() {
        AppOptions.appWithoutSidebar = true;
        AppOptions.appWithoutHeader = true;
        AppOptions.appContentFullWidth = true;
    },
    beforeRouteLeave (to, from, next) {
    AppOptions.appWithoutSidebar = false;
    AppOptions.appWithoutHeader = false;
    AppOptions.appContentFullWidth = false;
    next();
    },
    methods: {
        checkForm: function (e) {
            e.preventDefault();
            let validation = this.validate();
            console.log("validation", validation);
            
            if (validation) {
                if (this.username === "admin@mail.com" && this.password === "Test@123") {
                    this.$router.push({ path: '/dashboard' })
                } else {
                    this.loginValidation = true;
                    this.loginValidationMsg = 'Login failed. Please check your credentials.';
                }
            }
        },
        validate() {
            let status = true;

            if (this.username.trim() === '' || this.password.trim() === '') {
                this.loginValidation = true;
                this.loginValidationMsg = 'Username or password cannot be empty.';
                status = false;
            }else{
                this.loginValidation = false;
                this.loginValidationMsg = '';
            }
            
            return status;
        }
    }
}
</script>

<style>
#login-content-div {
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    background-color: white;
}

.home-image-div {
    height: 100%;
}

.home-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
</style>